import React,{useState , useEffect} from 'react'
import './style.css'
import Alert from './Alert'
import Doitem from './Doitem'


 const localstorag = ()=>{
   let dolist = localStorage.getItem('dolist')
   if(dolist){
     return JSON.parse(localStorage.getItem('dolist'));
   }
   else{
     return []
   }
 }

function Main() {
 const [name , setName] = useState('')
 const [dolist , setDolist] = useState(localstorag)
 const [isEdit,setIsEdit] = useState(false)
 const [editId , setEditId]=useState(null)
 const [alert , setAlert] = useState({show:false , msg:'' , type:''})
 



 const DeleteItem=(id)=>{
  const newitems = dolist.filter((item)=> item.id !== id)
  setDolist(newitems)
  setAlertComponent(true , 'Item removed' , 'danger')
 }
 ///////////

 const handleSupmit=(e)=>{
  e.preventDefault();
  console.log('hellow')
  if(!name){
   setAlertComponent(true , 'Please Add Somthing' , 'danger')
  }
  else if( name && isEdit)
  {
    setDolist(dolist.map((item) => {
      if(item.id === editId){
        return{...item , title:name}
      }
      return item
    }))
    setName('')
    setIsEdit(false)
    setEditId(null)
    setAlertComponent(true , 'Eddit successfully' , 'success')
  }else{
   const item = {id: new Date().getTime().toString() , title:name}
   setDolist([...dolist , item])
   setAlertComponent(true , 'Item added' , 'success')
   setName('')
  }
 }

 const setAlertComponent = (show=false , msg='' , type='')=>{
  setAlert({show:show , msg , type})
 }

 const editHandler = (id)=>{
    const edititem = dolist.find((item)=> item.id === id)
    setIsEdit(true)
    setEditId(id)
    setName(edititem.title)
 }

 useEffect(()=>{
  localStorage.setItem('dolist',JSON.stringify(dolist))
 },[dolist])
 return (
  <div className='container'>
   <div className="form-container">
    <h4 className="header">Sticky Note</h4>
    <form onSubmit={handleSupmit}>
     <div className="input-group">
      <input placeholder="e.g.back a cake"  type="text" maxLength="60" value={name} onChange={(e)=> setName(e.target.value) } />
      <button type='submit' className='btn'>{isEdit? 'edit' : 'supmit'}</button>
     </div>
    </form>
    <div className="clearAll">
      <button onClick={()=> setDolist([])} className="btn">Clear All</button>
    </div>
    {alert.show && <Alert {...alert}  setAlertComponent={setAlertComponent}/>}
   </div>
   <br></br>
   
   <div className="todolist-container">
    {/* map after here */}
    {
     dolist.map((item)=>{
       return(
        < Doitem {...item} DeleteItem={DeleteItem} editHandler={editHandler} />
       )
     })
    }
    {/* map end here */}
   </div>
  </div>
 )
}

export default Main
