import React from 'react'
import { FaBan ,FaEdit } from "react-icons/fa";
import note from './note.png'

function Doitem({id , title ,DeleteItem ,editHandler}) {
 
 return (
  <div className='itemcontainer' key={id}>
       <div className="todoitem"> 
       <img src={note} alt=""/>
       <p id={id}>{title}</p>
       <div className="btnContainer">

       <button className='btn2' onClick={()=>editHandler(id)}><FaEdit className='edit' /></button>

       <button onClick={()=>DeleteItem(id)} className='btn2'><FaBan className='delet'/></button>

      </div>
      </div>
      
    </div>
 )
}

export default Doitem
